<template>
  <b-tbody class="my_tb">
    <!-- Search By Title or Author -->
    <b-tr class="bookcard">
      <b-td class="bookcard_img">
        <img
          v-if="book.cover_i"
          :src="`http://covers.openlibrary.org/b/id/${book.cover_i}-S.jpg`"
          alt="cover"
        />
        <img
          v-else-if="book.cover && !book.cover_i"
          :src="`${book.cover.small}`"
          alt="cover"
        />
        <img
          v-else
          class="cover_not_found"
          src="../../assets/img/NoImage.png"
          alt="not"
        />
      </b-td>
      <b-td class="bookcard__details">
        <div v-if="book.title" class="bookcard__title">
          <span>
            {{ book.title }}
          </span>
        </div></b-td
      >
      <b-td class="bookcard__authors">
        <div v-if="book.author_name">
          <div v-if="book.author_name && book.author_name.length == 1">
            <span>
              {{ book.author_name[0] }}
            </span>
          </div>
          <div v-if="book.author_name && book.author_name.length > 1">
            <span> {{ book.author_name[0] }} and others </span>
          </div>
        </div>

        <div v-if="book.authors">
          <div v-if="book.authors && book.authors.length == 1">
            <span>
              {{ book.authors[0].name }}
            </span>
          </div>
          <div v-if="book.authors && book.authors.length > 1">
            <span> {{ book.authors[0].name }} and others </span>
          </div>
        </div>
      </b-td>
      <b-td class="bookcard__year hide_sm">
        <div v-if="book.first_publish_year" class="bookcard__title">
          <span>
            {{ book.first_publish_year }}
          </span>
        </div>
        <div v-else-if="book.publish_date">
          <span>
            {{ book.publish_date }}
          </span>
        </div>
      </b-td>
      <b-td class="bookcard__isbn hide_sm">
        <div v-if="book.isbn">
          <span>
            {{ book.isbn[0] }}
          </span>
        </div>
        <div v-if="book.identifiers">
          <span v-if="book.identifiers.isbn_13">
            {{ book.identifiers.isbn_13[0] }}
          </span>
          <span v-if="book.identifiers.isbn_10 && !book.identifiers.isbn_13">
            {{ book.identifiers.isbn_10[0] }}
          </span>
        </div>
      </b-td>
      <b-td class="bookcard__details">
        <div>
          <b-button
            size="sm"
            @click="modalShow = !modalShow"
            class="mr-1"
            variant="warning"
          >
            Info
          </b-button>
        </div></b-td
      >
      <b-modal
        v-model="modalShow"
        id="modal-tall"
        title="BookShock"
        centered
        scrollable
        ok-only
        hide-header-close
        :header-bg-variant="headerBgVariant"
        :header-text-variant="headerTextVariant"
        :body-bg-variant="bodyBgVariant"
        :body-text-variant="bodyTextVariant"
        :footer-bg-variant="footerBgVariant"
        :footer-text-variant="footerTextVariant"
      >
        <h3 class="text-center">{{ book.title }}</h3>
        <h4 v-if="book.subtitle" class="text-center">{{ book.subtitle }}</h4>
        <hr />
        <div v-if="book.author_name">
          <div v-if="book.author_name && book.author_name.length == 1">
            <h5 class="text-center">
              {{ book.author_name[0] }}
            </h5>
          </div>

          <div v-if="book.author_name && book.author_name.length > 1">
            <h5 class="text-center">{{ book.author_name[0] }} and others</h5>
          </div>
        </div>

        <div v-if="book.authors">
          <div v-if="book.authors && book.authors.length == 1">
            <h5 class="text-center">
              {{ book.authors[0].name }}
            </h5>
          </div>
          <div v-if="book.authors && book.authors.length > 1">
            <h5 class="text-center">{{ book.authors[0].name }} and others</h5>
          </div>
        </div>

        <div v-if="book.first_publish_year">
          <p class="text-center">
            {{ book.first_publish_year }}
          </p>
        </div>
        <div v-else-if="book.publish_date">
          <p class="text-center">
            {{ book.publish_date }}
          </p>
        </div>
        <div class="my_cover_modal">
          <b-img
            v-if="book.cover_i"
            class="img-fluid mc_modal text-center height-fit"
            center
            :src="`http://covers.openlibrary.org/b/id/${book.cover_i}-M.jpg`"
            alt="cover"
          />
          <img
            v-else-if="book.cover && !book.cover_i"
            :src="`${book.cover.medium}`"
            alt="cover"
          />
          <img
            v-else
            class="cover_not_found"
            src="../../assets/img/NoImage.png"
            alt="not"
          />
        </div>
        <br />
        <div class="extract" v-if="book.first_sentence">
          <span>{{ book.first_sentence[0] }}</span>
          <br />
          <br />
        </div>
        <div class="extract" v-if="book.excerpts">
          <span>{{ book.excerpts[0].text }}</span>
          <br />
          <br />
        </div>
        <div v-if="book.number_of_pages_median">
          <ul class="my_label">
            Pages:
            <li class="circle">{{ book.number_of_pages_median }}</li>
          </ul>
        </div>
        <div v-if="book.pagination">
          <ul class="my_label">
            Pages:
            <li class="circle">{{ book.pagination }}</li>
          </ul>
        </div>
        <div v-if="book.publishers && book.publishers.length == 1">
          <ul class="my_label">
            Publishers
            <li>
              {{ book.publishers[0].name }}
            </li>
          </ul>
        </div>
        <div v-if="book.publishers && book.publishers.length > 1">
          <ul class="my_label">
            Characters:
            <li v-for="publisher in book.publishers" :key="publisher.id">
              {{ publisher }}
            </li>
          </ul>
        </div>
        <div v-if="book.person">
          <div v-if="book.person && book.person.length == 1">
            <ul class="my_label">
              Characters:
              <li class="circle">
                {{ book.person[0] }}
              </li>
            </ul>
          </div>
          <div v-if="book.person && book.person.length > 1">
            <ul class="my_label">
              Characters:
              <li
                class="circle"
                v-for="character in book.person"
                :key="character.id"
              >
                {{ character }}
              </li>
            </ul>
          </div>
        </div>
        <div v-if="book.subject_people">
          <div v-if="book.subject_people && book.subject_peoplelength == 1">
            <ul class="my_label">
              Characters:
              <li class="circle">
                <a :href="book.subject_people[0].url" target="blank">{{
                  book.subject_people[0].name
                }}</a>
              </li>
            </ul>
          </div>
          <div v-if="book.subject_people && book.subject_people.length > 1">
            <ul class="my_label">
              Characters:
              <li
                class="circle"
                v-for="character in book.subject_people"
                :key="character.id"
              >
                <a :href="character.url" target="blank">{{ character.name }}</a>
              </li>
            </ul>
          </div>
        </div>
        <div v-if="book.place">
          <div v-if="book.place && book.place.length == 1">
            <ul class="my_label">
              Places:
              <li class="circle">
                <a :href="book.place[0].url" target="blank">{{
                  book.place[0]
                }}</a>
              </li>
            </ul>
          </div>
          <div v-if="book.place && book.place.length > 1">
            <ul class="my_label">
              Places:
              <li
                class="circle"
                v-for="location in book.place"
                :key="location.id"
              >
                {{ location }}
              </li>
            </ul>
          </div>
        </div>
        <div>
          <div v-if="book.subject_places && book.subject_places.length == 1">
            <ul class="my_label">
              Places:
              <li class="circle">
                <a :href="book.subject_places[0].url">{{
                  book.subject_places[0].name
                }}</a>
              </li>
            </ul>
          </div>
          <div v-if="book.subject_places && book.subject_places.length > 1">
            <ul class="my_label">
              Places:
              <li
                class="circle"
                v-for="location in book.subject_places"
                :key="location.id"
              >
                <a :href="location.url">{{ location.name }}</a>
              </li>
            </ul>
          </div>
        </div>
        <div v-if="book.links && book.links.length == 1">
          <span class="my_label">Links </span>
          <span>
            <a :href="`${book.links[0].url}`" target="blank">{{
              book.links[0].title
            }}</a>
          </span>
        </div>
        <div v-if="book.links && book.links.length > 1">
          <ul class="my_label">
            Links:
            <li class="circle" v-for="link in book.links" :key="link.id">
              <a :href="`${link.url}`" target="blank">{{ link.title }}</a>
            </li>
          </ul>
        </div>
      </b-modal>
    </b-tr>
  </b-tbody>
</template>

<script>
export default {
  name: "BookComp",
  data: () => ({
    modalShow: false,
    variants: [
      "primary",
      "secondary",
      "success",
      "warning",
      "danger",
      "info",
      "light",
      "dark",
    ],
    headerBgVariant: "dark",
    headerTextVariant: "danger",
    bodyBgVariant: "light",
    bodyTextVariant: "dark",
    footerBgVariant: "warning",
    footerTextVariant: "dark",
  }),
  props: {
    // Main.vue passa a Card.vue i seguenti dati...
    book: Object,
  },
  computed: {},
  methods: {},
};
</script>

<style lang="scss" scoped>
.my_tb {
  background: #fff;
}
.bookcard {
  &:nth-of-type(even) {
    background: #cecece;
  }
  .bookcard_img {
    text-align: center;
    img {
      height: 80%;
      border: 1px solid #cecece;
      &.cover_not_found {
        height: 60px;
      }
    }
  }
}
.extract {
  text-align: center;
  font-size: 1.5rem;
  font-style: italic;
  margin-bottom: 5px;
}
.my_label {
  color: red;
  font-weight: 900;
}
ul {
  padding: 0;
  margin: 0;
  li {
    list-style: square;
    color: rgb(0, 0, 0);
    margin-left: 20px;
    font-weight: 100;
  }
}
.circle {
  list-style: circle;
}
a {
  color: #007bff;
  &:hover {
    color: #0a2644;
  }
}
.my_cover_modal {
  display: flex;
  justify-content: center;
}
.cover_not_found_modal {
  height: 300px;
}
.hide_sm {
  @media only screen and (max-width: 767px) {
    display: none;
  }
}
</style>
