<template>
  <!---->
  <div class="about">
    <!-- catturo l'evento search lanciato in header -->
    <Search @searchQuery="searching" :setDefault="setDefault" />
    <!-- aggiungo un contenitore con background e descrizione pagina che visualizzo solo all'accesso -->
    <!-- utilizzo una condizione -->
    <div v-if="welcome" class="welcome_c">
      <div class="welcome">
        <div class="mc_bool">
          <img src="../assets/img/BookShock.png" alt="bookShock" />
          <!--<h3>Find your book</h3>-->
        </div>
      </div>
    </div>

    <div v-else>
      <div class="d-flex justify-content-center align-items-center mc_msg">
        <self-building-square-spinner
          v-if="loading"
          :animation-duration="6000"
          :size="40"
          color="#ff1d5e"
        />
      </div>

      <!-- se show Ã¨ true mostro l'h1 e il relativo messaggio -->
      <div class="d-flex justify-content-center align-items-center mc_msg">
        <h1 v-if="show" class="text-center text-danger">No results found</h1>
        <!-- <h1 v-if='results.movie.length === 0 && results.movie.length === 0'>nessun risultato</h1> -->
      </div>

      <div v-if="!loading && !show">
        <Main
          class="main"
          :bookList="bookList"
          :bookData="bookData"
          :apiPage="apiPage"
          @pagPrec="pagPrec"
          @pagSucc="pagSucc"
        />
      </div>
    </div>
  </div>
</template>
<script>
// importo in app...
import axios from "axios";
import Search from "../components/BookShock/Search.vue";
import Main from "../components/BookShock/Main.vue";
import { SelfBuildingSquareSpinner } from "epic-spinners";
export default {
  name: "BookshockView",
  // dichiaro i componenti esportati in APP
  components: {
    Search,
    Main,
    SelfBuildingSquareSpinner,
  },
  data() {
    return {
      apiBooksURL: "http://openlibrary.org/search.json",
      apiBooksIsbn: "https://openlibrary.org/api/books?bibkeys=ISBN:",
      welcome: true, // se true mostro un elemento di default con background di 'benvenuto'. Dopo la chiamata API diventa false
      show: false, // utilizzo la variabile show per mostrare o no il messaggio 'nessun rislutato trovato'
      apiQuery: "",
      selected: "",
      bookList: [],
      bookData: {},
      bookListLength: Number,
      apiPage: 1,
      loading: false,
    };
  },
  mounted() {
    //this.homeTrending(); // richiamo la funzione per popolare la home con nuova chiamata api per trending movies
  },
  methods: {
    // funzione che permette di eseguire la chiamata API avviando la ricerca di query(del testo inserito dall'utente)
    searching(text, type) {
      if (text != "") {
        this.welcome = false;
        this.show = false;
        this.loading = true;
        this.apiQuery = text;
        this.selected = type;
        let request = {
          params: {
            page: this.apiPage,
            limit: 10
          },
        };
        if (type == "isbn") {
          axios
            .get(this.apiBooksIsbn + text + "&jscmd=data&format=json", request)
            .then((result) => {
              console.log(result);
              this.loading = false;
              this.bookData = result.data;
              console.log(this.bookData);
              this.bookList = this.bookData;
              console.log(this.bookList);
              this.bookListLength = this.bookList.length;
              console.log(this.bookListLength);
              if (this.bookList.length === 0) {
                this.show = true; // v-if='show = true' mostra il messaggio 'Nessun risultato trovato'
              }
            })
            .catch((err) => {
              console.log(err);
            });
        } else {
          axios
            .get(this.apiBooksURL + "?" + type + "=" + text, request)
            .then((result) => {
              console.log(result);
              this.loading = false;
              this.bookData = result.data;
              console.log(this.bookData);
              this.bookList = result.data.docs;
              console.log(this.bookList);
              this.bookListLength = this.bookList.length;
              console.log(this.bookListLength);
              if (this.bookList.length === 0) {
                this.show = true; // v-if='show = true' mostra il messaggio 'Nessun risultato trovato'
              }
            })
            .catch((err) => {
              console.log(err);
              this.loading = false;
              this.show = true;
            });
        }
      }
    },
    // metodi scatenati al click dei bottoni prec. e succ. per ripetere la chiamata
    // API con numero di pagina aggiornato
    pagPrec(apiPage) {
      this.apiPage = apiPage;
      this.searching(this.apiQuery, this.selected);
    },
    pagSucc(apiPage) {
      this.apiPage = apiPage;
      this.searching(this.apiQuery, this.selected);
    },
    //funzione per resettare gli array
    reset() {
      this.bookList = [];
      this.welcome = true;
    },
    //funzione per la situazione di default,viene passata come prop per il bottone reset
    setDefault() {
      this.reset();
      this.apiPage = 1;
    },
  },
};
</script>

<style lang="scss" scoped>
// importo il foglio di stile general.scss
@import "../assets/style/app.scss";
// definisco il colore header
$color-header: #000000;
// definisco il colore per i bottoni
$color-btn: #e30d12;
.about {
  overflow: hidden;
  min-height: 100vh;
  padding-top: 0px;
  background: $color-header;
  @media only screen and (max-width: 992px) {
    padding-top: 0px;
  }
  @media only screen and (max-width: 550px) {
    padding-top: 50px;
  }
}
.main {
  padding-bottom: 150px;
  margin: 0 auto;
  @media all and (max-width: 600px) {
    padding-bottom: 50px;
  }
  @media all and (min-width: 601px) and (max-width: 750px) {
    padding-bottom: 62px;
  }
  @media all and (min-width: 751px) and (max-width: 800px) {
    padding-bottom: 141px;
  }
  @media all and (min-width: 801px) and (max-width: 900px) {
    padding-bottom: 150px;
  }
}
.welcome_c {
  display: flex;
  align-items: center;
  //height: 50vh;
  margin-top: 100px;
  @media all and (max-width: 800px) {
    //height: 60vh;
  }
}
.welcome {
  width: 100%;
  //height: 100%;
  //background-image: url("../assets/img/boolflix.jpg");
  background: $color-header;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
}
.mc_bool {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 30px;
  h1 {
    font-size: 3em;
    color: $color-btn;
    text-transform: uppercase;
    text-shadow: 0px 3px 2px rgba(12, 10, 10, 0.767);
  }
  h3 {
    font-size: 3em;
    @media all and (max-width: 400px) {
      font-size: 1em;
    }
    @media all and (max-width: 500px) {
      font-size: 2.2em;
    }
  }
  h4 {
    margin-bottom: 40px;
  }
  img {
    @media all and (max-width: 400px) {
      width: 70%;
    }
  }
}
.mc_msg {
  height: 75vh;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.main-t {
  padding-bottom: 140px;
  @media all and (max-width: 799px) {
    padding-bottom: 60px;
  }
}
</style>
