<template>
  <header class="d-flex flex-column ps-5 pe-5">
    <div
      class="mysearch d-flex align-items-center justify-content-between ps-5 pe-5"
    >
      <div class="logo">
        <button class="bool_btn" @click.prevent="resetAll()">
          <img src="../../assets/img/BookShock.png" alt="bookshock" />
        </button>
      </div>
      <div>
        <form class="d-flex mt-3 mb-3 myform">
          <div>
            <!-- con v-model faccio il binding di textToSearch, ovvero del testo inserito nell'input -->
            <!-- con @keyup.enter faccio partire la funzione startSearch premendo invio -->
            <input
              v-model.trim="apiQuery"
              class="form-control"
              type="text"
              placeholder="Find your book"
            />
            <!--@keyup.enter="sendQuery"-->
          </div>
          <div class="">
            <!-- al click sul bottone faccio partire la funzione startSearch-->
            <!-- .prevent evita che si ricarichi la pagina ogni volta che clicco -->
            <button @click.prevent="sendQuery()" class="btn btn-primary mc_btn">
              Search
            </button>
          </div>
        </form>
      </div>
    </div>
    <div class="mt-3 d-flex flex-column align-items-center text-light">
      Search by: <strong> {{ selected }} </strong>
    </div>
    <div class="myfilter">
      <select v-model="selected">
        <option v-for="option in options" :key="option.name">
          {{ option.item }}
        </option>
      </select>
      <!--
      <b-form-radio-group
        v-model="selected"
        :options="options"
        value-field="item"
        text-field="name"
        disabled-field="notEnabled"
        name="radios-btn-default"
        buttons
        button-variant="outline-primary"
      >
      </b-form-radio-group>-->
    </div>
  </header>
</template>

<script>
export default {
  name: "SearchComp",
  data() {
    // in data carico la variabile textToSearch
    return {
      apiQuery: "",
      selected: "title",
      options: [
        { item: "title", name: "Title" },
        { item: "author", name: "Author" },
        { item: "isbn", name: "ISBN" },
      ],
    };
  },
  props: {
    setDefault: Function,
    pageDefault: Function,
  },
  methods: {
    // funzione che fa partire la ricerca
    sendQuery() {
      this.setDefault();
      // $emit 'search' Ã¨ un evento che viene letto dal padre (APP)
      // ho bisogno di due informazioni: il nome dell'evento (search) e un oggetto in cui ho il testo da cercare e il tipo di ricerca da effettuare
      this.$emit("searchQuery", this.apiQuery, this.selected);
      console.log(this.apiQuery);
      console.log(this.selected);
      // svuoto la stringa di ricerca
      this.apiQuery = "";
    },
    resetAll() {
      this.apiQuery = ""; //svuoto il campo di ricerca
      this.setDefault(); //richiamo la funzione setDefault passata come props
    },
  },
};
</script>

<style lang="scss" scoped>
/* importo le variabili */
header {
  // definisco il colore header
  $color-header: #000000;
  // definisco il colore per i bottoni
  $color-btn: #0563af;
  background: $color-header;
  //height: 65px;
  justify-content: space-between;
  @media only screen and (max-width: 450px) {
    justify-content: center;
    flex-direction: column;
  }
  @media only screen and (max-width: 400px) {
  }
  .logo {
    color: $color-btn;
    text-transform: uppercase;
    font-weight: 700;
    margin: 5px;
    margin-right: 10px;
    .bool_btn {
      background: #000000;
      border: #000000;
      &:hover {
        img {
          /* Start the shake animation and make the animation last for 0.5 seconds */
          animation: shake 0.5s;
          /* When the animation is finished, start again */
          animation-iteration-count: 5s;
        }
      }
      img {
        max-height: 60px;
      }
    }
    span {
      &:last-child {
        font-size: 2em;
      }
    }
  }
  .mc_btn {
    background-color: $color-btn;
    border-color: transparent;
    margin-left: 3px;
    &:hover {
      filter: brightness(1.2);
    }
  }
  strong {
    text-transform: uppercase;
  }
  .myfilter {
    display: flex;
    justify-content: center;
    align-items: center;
    select {
      background-color: #0563af;
      color: white;
      padding: 12px;
      width: 250px;
      border: none;
      font-size: 20px;
      box-shadow: 0 5px 25px rgba(0, 0, 0, 0.2);
      -webkit-appearance: button;
      appearance: button;
      outline: none;
      text-transform: capitalize;
      &:hover {
        cursor: pointer;
      }
    }
  }
}
@media only screen and (max-width: 550px) {
  .form-control {
    width: auto;
  }
  .mysearch {
    flex-direction: column;
  }
}
@media only screen and (max-width: 450px) {
  button {
    &.mc_btn {
      display: none;
    }
  }
  div.logo span:first-child {
    display: none;
  }
  div.logo span:last-child {
    display: block;
  }
}
@media only screen and (min-width: 451px) {
  div.logo span:last-child {
    display: none;
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
</style>
