<template>
  <main class="container">
    <div class="d-flex justify-content-between align-items-center">
      <b-button
        pill
        variant="warning"
        v-if="this.bookList.length > 1"
        :disabled="bookData.start === 0"
        @click="$emit('pagPrec', apiPage - 1)"
      >
        Back
      </b-button>
      <div v-if="this.bookList.length > 1">
        <div class="text text-light">
          page: {{ bookData.start / 10 + 1 }} /
          {{ Math.ceil(bookData.numFound / 10) }}
        </div>
      </div>
      <b-button
        pill
        variant="warning"
        v-if="this.bookList.length > 1"
        :disabled="
          bookData.start / 10 + 1 > Math.floor(bookData.numFound / 10) ||
          bookData.start == bookData.numFound - 10
        "
        @click="$emit('pagSucc', apiPage + 1)"
      >
        Next
      </b-button>
    </div>
    <div v-if="this.bookList.length != 0">
      <h1 class="text-center mt-3 mb-3 text-light">Books Found</h1>
      <b-table-simple responsive>
        <b-thead class="my_th">
          <b-tr>
            <b-th class="p-1">Cover</b-th>
            <b-th class="p-1">Title</b-th>
            <b-th class="p-1">Author</b-th>
            <b-th class="p-1 hide_sm">Publish Date</b-th>
            <b-th class="p-1 hide_sm">ISBN</b-th>
            <b-th class="p-1">Info</b-th>
          </b-tr>
        </b-thead>

        <Book :book="book" v-for="book in bookList" :key="book.id" />

        <b-tfoot class="my_th">
          <b-tr>
            <b-th class="p-1">Cover</b-th>
            <b-th class="p-1">Title</b-th>
            <b-th class="p-1">Author</b-th>
            <b-th class="p-1 hide_sm">Publish Date</b-th>
            <b-th class="p-1 hide_sm">ISBN</b-th>
            <b-th class="p-1">Info</b-th>
          </b-tr>
        </b-tfoot>
      </b-table-simple>
    </div>
  </main>
</template>

<script>
import Book from "./Book.vue";

export default {
  name: "MainComp",
  components: { Book },
  props: {
    bookList: Array,
    bookData: Object,
    apiPage: Number,
  },
  data() {
    return {};
  },
  mounted() {},
  methods: {},
};
</script>

<style lang="scss" scoped>
.my_th {
  background-color: #0093e9;
  background-image: linear-gradient(160deg, #0093e9 0%, #80d0c7 100%);
  .hide_sm {
    @media only screen and (max-width: 767px) {
      display: none;
    }
  }
}
</style>
